.home {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
  cursor: default;
  .section1 {
    box-sizing: border-box;
    position: relative;
    height: 45rem;
    max-width: 100%;
    padding: 0 5rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    color: white;
    transition: 1000ms;
    overflow: hidden;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: #0000006c;
    }
    img {
      position: absolute;
      object-fit: cover;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      transform: scale(1.05);
      transition: transform 1000ms;
    }
    &:hover > img {
      transform: scale(1);
    }
    .cta {
      display: flex;
      flex-direction: column;
      gap: 2rem;
      z-index: 2;
      max-width: 35rem;
      h1,
      h3,
      a,
      .shop-button {
        font-weight: 400;
        margin: 0;
        text-decoration: none;
      }
      h1 {
        font-size: 3.5rem;
        text-shadow: black 0 0 15px;
      }
      h3 {
        font-size: 1.5rem;
        font-weight: 300;
        text-shadow: black 2px 2px 1px, black 0 0 10px;
      }
      .shop-button {
        width: fit-content;
        color: black;
        position: relative;
        background: rgb(255, 255, 255);
        box-shadow: #ff623a 0px 0px 0px 0px;
        // outline: #ff623a solid 4px;
        // outline-offset: 4px;
        border-radius: 5px;
        font-size: 1.5rem;
        padding: 0.6em 1em;
        transition: 200ms;
        &:hover {
          box-shadow: #a83232 0px 0px 0px 4px, #ff623a 0px 0px 0px 8px;
        }
      }
    }
  }
  .section2 {
    box-sizing: border-box;
    min-height: 40rem;
    padding: 4rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem;
    h1 {
      font-weight: 200;
      font-size: 2.5rem;
      margin: 0;
    }
    .cards {
      box-sizing: border-box;
      width: 100%;
      flex: 1 1;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 2rem;
      padding: 4rem 2rem;
      .card,
      a {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex: 1 1 auto;
        height: 100%;
        width: 100%;
        background-color: #ececec;
        transition: 300ms;
        img {
          position: absolute;
          max-width: 90%;
          max-height: 90%;
        }
        p {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 70%;
          padding: 0 0.5rem;
          height: 3rem;
          font-size: 1rem;
          margin: 0;
          position: absolute;
          top: -20px;
          text-align: center;
          color: white;
          background: rgb(83, 83, 83);
          transition: 500ms ease-in-out;
          opacity: 0;
        }
        &:hover {
          box-shadow: #0000000a 0px 10px 10px;
        }
        &:hover > p {
          opacity: 1;
        }
      }
    }
  }
  .section3 {
    box-sizing: border-box;
    width: 100%;
    height: 50rem;
    background: #f7f7f7;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      transform: scale(1.05);
      transition: transform 1000ms;
      &:hover {
        transform: scale(1);
      }
    }
    .top {
      width: 100%;
      height: 50%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        width: 50%;
        height: 100%;
        overflow: hidden;
      }
      .right {
        padding: 2rem;
        max-width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 2rem;
        h3,
        p {
          font-size: 3rem;
          font-weight: 400;
          margin: 0;
        }
        p {
          font-weight: 200;
          font-size: 2rem;
        }
      }
    }
    .bottom {
      width: 100%;
      height: 50%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .right {
        overflow: hidden;
        width: 50%;
        height: 100%;
      }
      .left {
        padding: 2rem;
        height: 50%;
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 2rem;
        h3,
        p {
          font-size: 3rem;
          font-weight: 400;
          margin: 0;
        }
        p {
          font-weight: 200;
          font-size: 2rem;
        }
      }
    }
  }
  .section4 {
    box-sizing: border-box;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 6rem;
    padding: 8rem 2rem;
    h1 {
      margin: 0;
      font-size: 2.5rem;
      font-weight: 400;
    }
    .bottom {
      max-width: 100%;
      display: flex;
      flex: 1;
      justify-content: space-evenly;
      align-items: flex-start;
      gap: 2rem;
      .quote {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        max-width: 20rem;
        .img {
          background-color: grey;
          height: 15rem;
          width: 15rem;
          border-radius: 50%;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
        }
        .img1 {
          background-image: url("../assets/sustainable.jpg");
        }
        .img2 {
          background-image: url("../assets/ethical.jpg");
        }
        .img3 {
          background-image: url("../assets/fashionable.jpg");
        }
        h3 {
          margin: 0;
          font-weight: 300;
          font-size: 1.5rem;
        }
        p {
          margin: 0;
          font-weight: 200;
          font-size: 1rem;
          text-align: center;
        }
      }
    }
  }
}
@media only screen and (max-width: 850px) {
  .home {
    .section1 {
      align-items: center;
    }
    .section2 {
      .cards {
        grid-template-columns: repeat(2, 1fr);
        .card,
        a {
          height: 20rem;
        }
      }
    }
    .section3 {
      .top,
      .bottom {
        .right,
        .left {
          h3,
          p {
            font-size: 1.5rem;
          }
        }
      }
    }
    .section4 {
      h1 {
        text-align: center;
      }
      .bottom {
        flex-direction: column;
        gap: 4rem;
        .quote {
          max-width: 40rem;
        }
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .home {
    .section3 {
      img {
        width: 15rem;
      }
      .top,
      .bottom {
        .right,
        .left {
          h3,
          p {
            font-size: 1.5rem;
          }
        }
      }
    }
  }
}
