.shop {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  color: black;
  .top {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: solid 1px rgba(0, 0, 0, 0.199);
    .gender-section {
      display: flex;
      gap: 0.5rem;
      max-width: 100%;
      padding: 0 1rem;
      button {
        cursor: pointer;
        background: none;
        color: black;
        font-weight: 800;
        font-size: 1.2rem;
        letter-spacing: 1px;
        border: none;
        padding: 0.5rem 1rem;
        &:hover {
          background-color: #00000015;
        }
      }
      button.active {
        color: white;
        background-color: #242424;
      }
    }
    .categories {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      padding: 0 1rem;
      gap: 1rem;
      .button {
        display: flex;
        gap: 1rem;
      }
      button {
        transition: 200ms;
        cursor: pointer;
        background: none;
        color: black;
        border: none;
        font-size: 1rem;
        &:hover {
          background: #f5f5f5;
        }
      }
      button.active {
        transition: 200ms;
        background: #ffdcd3c2;
        box-shadow: #00000036 0 3px 5px;
        transform: translateY(-3px);
      }
      input {
        height: 1.5rem;
        width: 15rem;
        outline: none;
        border: solid black 1px;
        transition: 200ms;
        padding: 0 0.2rem;
        margin: 0 1rem;
        &:focus {
          background: #ffdcd357;
          box-shadow: #00000036 0 0 5px;
        }
      }
    }
  }
  ul {
    padding: 2rem;
    a {
      text-decoration: none;
      color: black;
    }
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    max-width: 60rem;
    list-style: none;
    li {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .image-container {
        position: relative;
        background-color: #f7f7f7;
        height: 15rem;
        width: 15rem;
        padding: 0.5rem;
        margin-bottom: 0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          object-position: center;
          max-height: 100%;
          max-width: 100%;
          object-fit: cover;
          transition: 1000ms;
        }
        &:hover > img {
          transform: scale(0.95);
        }
      }
      p {
        max-width: 15rem;
        justify-self: flex-start;
        align-self: flex-start;
        margin: 0;
        font-weight: 300;
      }
      .name {
        height: 2.5rem;
      }
    }
  }
}

@media only screen and (max-width: 450px) {
  .shop {
    .top {
      .gender-section {
      }
      .categories {
        justify-content: space-evenly;
        input {
          width: 100%;
        }
        .button {
          width: 100%;
          justify-content: space-evenly;
          gap: 0;
        }
        button {
          font-size: 0.9rem;
        }
      }
    }
  }
}
